
.nav-link {
    color:#a9afb7;
    text-decoration: none;
    cursor: pointer;
} 
#resume-button-1{
    /* margin-top: -10px; */
    padding: 0px 30px;
    border-radius: 20px;
background-color:#a9afb7;
color: rgb(20, 67, 85);
border: none;
}

#nav-menu{
    
display: grid;
justify-content: space-between;  
grid-template-columns: 10% 40% 10%;
gap:10%; 
position: fixed;
top: 0;
width: 100%;
height:80px;
background-color:rgb(20, 67, 85);
z-index: 1;
}
.logo{
    display:flex;
    justify-content: center; 
    align-items:center ;
    
}
.mid-nav{
    display:flex;
justify-content:space-evenly;
align-items: center;
/* gap:5%; */
/* border:1px solid red; */
height: 100%;

}
#resume-button-1:hover{
    overflow:auto;
    opacity:inherit;
}
.navbar{
    display: grid;
 justify-content: space-between;  
grid-template-columns: 10% 40% 10%;
 gap:10%; 
position: fixed;
top: 0;
width: 100%;
height:80px;
background-color:rgb(20, 67, 85);
z-index: 1;
}

.footer-nav{
    display: none;
}
.navbar > div{
    /* border: 1px solid blue; */
}
@media all and (min-width:900px) and (max-width:1140px){
    #nav-menu{
        display: none;
    }

}

@media all and (min-width:100px) and (max-width:919px){
    #nav-menu{
        display: none;
    }

    .footer-nav{
        display: grid;
     justify-content: space-between;  
    grid-template-columns: 10% 40% 10%;
     gap:10%; 
    position: fixed;
    bottom: 0;
    width: 100%;
    height:80px;
    background-color:rgb(19, 2, 115);;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   z-index: 1; 
    }
    .footer-nav{
        display: block;
    }

}